const Checkout = ({ product }) => {
    return (
        <div>
            <div>
                <p><img src={product.url} alt="kuva tuotteesta"/></p>
                <p>{product.product} </p>
                <p id="counter">{product.counter}</p>
            </div>
        </div>
    )
}

const Cart = ({ products, setProducts }) => {
    const calcTotal = () => {
        const price = products.map(e => e.hinta * e.counter);
        return price.reduce((a, b) => Math.round((a + b + Number.EPSILON) * 100) / 100)
    }
    const zeroProducts = () => {
        const tempProducts = products.map(p => {
            p = { ...p, counter: 0 };
            return p;
        });
        setProducts(tempProducts);
    }
    const filteredProducts = products.filter(p => p.counter > 0)
    return (
        <div className="cart">
            {filteredProducts.map(c => <Checkout key={c.id} product={c} />)}
            <p>Yhteensä: {calcTotal()}€</p>
            <button onClick={() => zeroProducts()}>Tyhjennä</button>
        </div>
    )
}
export default Cart;