import "../App.css";

const Product = ({ product, lisaaostos, poistaostos, cart }) => {
    return (
        <tbody>
            <tr id="tiedot">
             <td>{product.product}</td>
             
             <td>{product.hinta}€</td>

             <td><img src={product.url} alt="kuva tuotteesta"/></td>
             {!!!cart && <td><p style={{paddingRight: 5}}><button onClick={() => lisaaostos(product.id)}>+</button> {product.counter}</p></td>}
             <td><button onClick={() => poistaostos(product.id)}>-</button></td>       

             </tr>
        </tbody>
    );
};



const Products = ({ products, setProducts}) => {
    
    const lisaaostos = id => {
        const ostokset = products.map(p => {
            if (id === p.id) {
                return { ...p, counter: p.counter + 1 };
            } else return p;
        })
        setProducts(ostokset);
    }
    const poistaostos = id => {
        const ostokset = products.map(p => {
            if (id === p.id && p.counter > 0) {
                return { ...p, counter: p.counter - 1 };
            } else return p;
        })
        setProducts(ostokset);
    }
    return (
        <div>
            {products.map(p =>
                <Product key={p.id} product={p} lisaaostos={lisaaostos} poistaostos={poistaostos}/>
            )}
        </div>
    );
};
export default Products;
