import './App.css';
import {productData} from './components/ProductData';
import Products from './components/ProductInfo';
import React, { useState,} from 'react';
import Cart from './components/Cart';


function App() {
  const [products, setProduct] = useState(productData);
  const[show,setShow] = useState(false);
  const[showCheckout, setShowCheckout] = useState(false);
  const[showLast, setShowLast] = useState(false);
  const[checkout, setCheckout] = useState({name: "", address: ""})

  const checkoutHandler = (c, info) => {
      const _checkout = { ...checkout };
      _checkout[info] = c.target.value;
      setCheckout(_checkout);
}

  const filteredProducts = products.filter(p => p.counter > 0)
  return (
  <div className="App">
      <header className="App-header">
        <p>Fanikauppa</p>
        <img src="https://i.imgur.com/j3owMWw.png" alt="ostoskärry" onClick={()=>setShow(!show)}/>
      </header>
    <div className="Products" style={{width: 350}}>
      <Products products={products} setProducts={setProduct}/>
    </div>
    {show &&
    filteredProducts.length > 0 ?
    <div id="ostoskori">
      <div id="cartwrapper">
        <Cart id="cart" products={products} setProducts={setProduct} />
      </div>
      {filteredProducts.length > 0 && <button onClick={()=>setShowCheckout(!showCheckout)}>Checkout</button>}
    </div> : 
        show && <div id="ostoskori">
        <p>Ei tuotteita ostoskorissa</p>
      </div>
    }
    {showCheckout &&
    <>
    <form id="form" style={{borderRadius: 10}}>
      <label>
        Name:
        <input type="text" name="name" value={checkout.name} onChange={c => checkoutHandler(c, 'name')} required />
        <br></br>
      </label>
      <label>
        Email:
        <input type="text" email="email" required/>
        <br></br>
      </label>
      <label>
        Phone:
        <input type="text" phone="phone" required/>
        <br></br>
      </label>
      <label>
        Address:
        <input type="text" address="address"  value={checkout.address} onChange={c => checkoutHandler(c, 'address')} required/>
        <br></br>
      </label>
      <label>
        Postcode:
        <input type="text" postcode="postcode" required/>
        <br></br>
      </label>
      <label>
        Postal district:
        <input type="text" district="district" required/>
        <br></br>
      </label>
    </form>
    <button style={{position:'relative', marginRight: '5%'}} onClick={()=>setShowLast(!showLast)}>Confirm order</button></>}

    {showLast &&
    <div id="modal">
    </div>}
    {showLast &&
      <div id="insidemodal" style={{borderRadius: 10}}>
          <p>Kiitos tilauksesta {checkout.name}!</p>
          <p>Tilauksesi: <Cart id="cart" products={products}/></p>
          <button style={{marginBottom: 10}} onClick={()=>[setShowLast(!showLast), setShow(!show), setShowCheckout(!showCheckout)]}>Sulje tilaus</button>
      </div>}

    
  </div>
  );
}

export default App;
